import { LitElement, html } from 'lit-element';
import { HAXWiring } from "@lrnwebcomponents/hax-body-behaviors/lib/HAXWiring.js"
/**
 * `count-up`
 * `count up js wrapper with minimal styling`
 *
 * @microcopy - language worth noting:
 *  -
 *
 * @customElement
 * @lit-html
 * @lit-element
 * @demo demo/index.html
 */
class CountUp extends LitElement {
  
  // render function
  render() {
    return html`
<style>:host {
  display: block;
}

:host([hidden]) {
  display: none;
}
</style>
<slot></slot>
<div>${this.start}</div>
<div>${this.end}</div>
<div>${this.duration}</div>
<div>${this.noEasing}</div>`;
  }

  // haxProperty definition
  static get haxProperties() {
    return {
  "canScale": true,
  "canPosition": true,
  "canEditSource": false,
  "gizmo": {
    "title": "Count up",
    "description": "count up js wrapper with minimal styling",
    "icon": "icons:android",
    "color": "green",
    "groups": [
      "Up"
    ],
    "handles": [
      {
        "type": "todo:read-the-docs-for-usage"
      }
    ],
    "meta": {
      "author": "btopro",
      "owner": "The Pennsylvania State University"
    }
  },
  "settings": {
    "quick": [],
    "configure": [
      {
        "property": "start",
        "description": "",
        "inputMethod": "textfield",
        "required": false,
        "icon": "icons:android"
      },
      {
        "property": "end",
        "description": "",
        "inputMethod": "textfield",
        "required": false,
        "icon": "icons:android"
      },
      {
        "property": "duration",
        "description": "",
        "inputMethod": "textfield",
        "required": false,
        "icon": "icons:android"
      },
      {
        "property": "noEasing",
        "description": "",
        "inputMethod": "boolean",
        "required": false,
        "icon": "icons:android"
      }
    ],
    "advanced": []
  }
};
  }
  // properties available to the custom element for data binding
  static get properties() {
    return {
  "start": {
    "name": "start",
    "type": "Number",
    "value": "0",
    "reflectToAttribute": false,
    "observer": false
  },
  "end": {
    "name": "end",
    "type": "Number",
    "value": "",
    "reflectToAttribute": false,
    "observer": false
  },
  "duration": {
    "name": "duration",
    "type": "Number",
    "value": "2.5",
    "reflectToAttribute": false,
    "observer": false
  },
  "noEasing": {
    "name": "noEasing",
    "type": "Boolean",
    "value": "",
    "reflectToAttribute": false,
    "observer": false
  }
};
  }

  /**
   * Store the tag name to make it easier to obtain directly.
   * @notice function name must be here for tooling to operate correctly
   */
  tag() {
    return "count-up";
  }

  // life cycle
  constructor() {
    super();
    
  }
  /**
   * life cycle, element is afixed to the DOM
   */
  connectedCallback() {
    super.connectedCallback();
    this.HAXWiring = new HAXWiring();
    this.HAXWiring.setup(CountUp.haxProperties, CountUp.tag, this);
  }
  // static get observedAttributes() {
  //   return [];
  // }
  // disconnectedCallback() {}

  // attributeChangedCallback(attr, oldValue, newValue) {}
  
}
customElements.define("count-up", CountUp);
export { CountUp };
